        <br />
        <p class="text-landing">
          Chng ti s dng cc cng ngh tt nht, cung cp dch v phn phi
          v pht hnh m nhc ca bn ti khp cc nn tng trn ton th gii.
          Cng vi , chng ti lin tc cp nht cc nn tng mi v cung cp
          cng c qun l, phn tch hiu sut, bo co doanh thu cho cc ngh
          s, ngi qun l v hng thu m. Bn ch cn tp trung vo vic to
          ra cc sn phm cht lng cn chng ti s m nhn cc vn  xung
          quanh vic pht trin m nhc k thut s ca bn.
        </p>
        <button type="button" class="btn btn-landing">Lin h ngay</button>
